@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../../fonts/Poppins-Medium.ttf") format("ttf");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../../fonts/Poppins-Bold.ttf") format("ttf");
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  color: inherit;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  position: relative;
  top: 0;
  padding: 12px 26px;
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  border: none;
  background: #ffa501;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  transition: background-color 0.5s ease 0s;
  cursor: pointer;
}
.button:hover {
  background-color: #bb7d0a;
}
.button:active {
  top: 3px;
}
.hamburger {
  position: absolute;
  width: 36px;
  height: 21px;
  top: 25px;
  right: 15px;
  z-index: 2;
  cursor: pointer;
}
@media (min-width: 1440px) {
  .hamburger {
    top: 30px;
    right: 30px;
  }
}
.hamburger__toggle {
  padding: 0;
  width: 36px;
  height: 21px;
  top: 25px;
  right: 25px;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.hamburger__toggle::before {
  content: "";
  position: absolute;
  width: 36px;
  height: 3px;
  top: 1px;
  right: 0px;
  background-color: #000000;
  box-shadow: 0 9px 0 0 #000000, 0 18px 0 0 #000000;
}
.hamburger__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.7);
  box-shadow: 0 9px 0 0 rgba(0, 0, 0, 0.7), 0 18px 0 0 rgba(0, 0, 0, 0.7);
}
.page {
  height: 100%;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  line-height: 18px;
  font-weight: 500;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  max-width: 1440pxpx;
  margin: 0 auto;
  background-color: #ffffff;
  color: #000000;
}
.page-main {
  padding-bottom: 50px;
}
.main-nav {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100vh;
  z-index: 10;
  visibility: hidden;
  opacity: 0;
  transition: all 0.6s;
}
.main-nav--opened {
  left: 0;
  visibility: visible;
  opacity: 1;
}
.main-nav__toggle {
  position: absolute;
  padding: 0;
  width: 36px;
  height: 21px;
  top: 25px;
  right: 25px;
  z-index: 3;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  transform: rotate(45deg);
  transition: all 0.6s;
}
.main-nav__wrapper {
  position: relative;
  width: 250px;
  background-color: #ffa501;
  height: 100%;
  padding-top: 140px;
  transition: all 0.6s;
  z-index: 10;
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    width: 345px;
  }
}
.main-nav__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  opacity: 0.75;
}
.main-nav--opened .main-nav__toggle {
  top: 25px;
  right: 25px;
  width: 29px;
  height: 29px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  left: -3px;
  width: 31px;
  height: 3px;
  background-color: #000000;
  border-radius: 4px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-sizing: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.7);
}
.site-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(6, 1fr);
  row-gap: 15px;
  margin: 0;
  padding: 0 0 0 55px;
  list-style-type: none;
}
.site-list__item {
  position: relative;
}
.site-list__item::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  top: 10px;
  left: -35px;
  border: 1px solid #000000;
  border-radius: 50%;
  transition: all 0.6s;
}
.site-list__item:hover::before {
  background-color: #ffffff;
  border-color: #ffffff;
}
.site-list__link {
  color: #ffffff;
  font-size: 24px;
  line-height: 36px;
  font-weight: 700;
  text-transform: uppercase;
  transition: 0.6s;
}
.site-list__link:hover {
  color: #000000;
}
