@media (min-width: 1440px) {
  .contacts__list {
    column-gap: 25px;
  }
}
.contacts__group {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
}
@media (min-width: 768px) {
  .contacts__group--column {
    grid-column: span 2;
  }
}
.contacts__label {
  position: absolute;
  display: block;
  top: -9px;
  left: 12px;
  padding: 0 8px;
  background-color: #ffffff;
}
.contacts__input,
.contacts__message {
  padding: 14px 20px 13px 20px;
  border: 1px solid #000000;
  box-sizing: border-box;
}
.contacts__input:focus {
  border: 1px solid #ffa501;
}
.contacts__message {
  min-height: 143px;
  resize: vertical;
  padding: 20px;
}
.contacts__message:focus {
  border: 1px solid #ffa501;
}
.button--contacts {
  justify-self: center;
}
@media (min-width: 768px) {
  .button--contacts {
    justify-self: start;
  }
}
#form * {
  outline: none;
}
.checkbox__input {
  display: none;
}
.checkbox__label {
  position: relative;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.checkbox__label::before {
  content: "";
  width: 11px;
  height: 11px;
  background-color: #ffffff;
  border: 1px solid #000000;
  margin-right: 10px;
}
.checkbox__label::after {
  content: "";
  width: 7px;
  height: 7px;
  position: absolute;
  top: 6px;
  left: 3px;
  background-color: #ffa501;
  transform: scale(0);
  transition: transform 0.5s ease 0s;
}
.checkbox__input:checked + .checkbox__label::after {
  transform: scale(1);
}
.socials-list {
  display: flex;
  flex-wrap: wrap;
  height: 30px;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.socials-list--menu {
  position: absolute;
  bottom: 200px;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 767px) {
  .socials-list--menu {
    bottom: 270px;
  }
}
.socials-list__item:not(:last-child) {
  margin-right: 11px;
}
.socials-list__link {
  display: inline-block;
}
.socials-list__icon:hover {
  color: #ffa501;
  transition: all 0.6s;
}
.socials-list__icon--menu:hover {
  color: #ffffff;
  transition: all 0.6s;
}
.privacy {
  padding: 50px 47px;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
}
.privacy__title {
  line-height: 24px;
  font-weight: 700;
  text-align: center;
}
.privacy__list {
  list-style-type: none;
}
.privacy__list--ol {
  padding: 0;
}
@media (max-width: 767px) {
  .privacy__list {
    padding: 0;
  }
}
.overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.56);
}
.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
  min-height: 400px;
  background-color: #f2f2f2;
  padding: 38px 40px 40px 40px;
}
.modal__close {
  position: absolute;
  top: 0;
  right: -40px;
  font-size: 41px;
  line-height: 21px;
  color: #000000;
  cursor: pointer;
}
.modal__subtitle {
  color: #0d0d0d;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}
.modal__descr {
  color: #0d0d0d;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
}
.modal__mini {
  min-height: 140px;
}
.modal .error {
  border: 1px solid red;
}
.modal label.error {
  border: none;
  text-align: center;
  margin-bottom: 15px;
}
#thanks {
  display: none;
}
